import React, { Component } from 'react';

class Info extends Component {
    render() {
        const { playerInfo } = this.props;
        return (
            <div className='info'>
            	<div className='info-text'>{' ' + playerInfo.level}</div>
            	<div className='info-text'>{' ' + playerInfo.health}</div>
            	<div className='info-text'>{' ' + playerInfo.attack}</div>
            	<div className='info-text'>{' ' + playerInfo.weapon}</div>
            	<div className='info-text'>{' ' + playerInfo.nextLevel}</div>
            	<div className='info-text'>{' ' + playerInfo.floor}</div>
            	<button className='info-text' onClick={this.props.onDarknessChange}></button>
            </div>
        );
    }
}

export default Info;



